.arbook-details {
    margin-top: 8rem;
}

.arbook-detail-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: var(--box-border-radius);

}

.arbook-detail-right {
    /* height: 300px; */
    text-align: left;
}

.arbook-detail-right-text {
    /* height: 300px; */
    overflow: hidden;
    text-align: left;
}

.arbook-detail-right-text h2 {
    margin: 0;
    color: var(--primary-color);
}

.arbook-detail-right-text p {
    text-align: justify;
    overflow: hidden;
}


.arbook-detail-button {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
}



.arbook-detail-button button {
    border-radius: var(--button-border-radius);
    height: 2.2rem;
    width: 14rem;
    border: none;
    cursor: pointer;
    font-weight: 900;
}

.arbook-detail-btn-left {
    background-color: var(--primary-color);
    color: #fff;
}

.arbook-detail-btn-right {
    background-color: var(--secondary-color);
    color: var(--primary-color);
}


@media (max-width: 900px) {
    .arbook-detail-button {
       flex-direction: column;
       align-items: center;
       gap: 1rem;
      
    }


    .arbook-detail-button button{
        width: 100%;
     }

}